}
60%{opacity:1;transform:scale3d(0.475, 0.475, 0.475) translate3d(-10px, 0, 0);animation-timing-function:cubic-bezier(0.175, 0.885, 0.32, 1)
}
}
.zoomInRight{animation-name:zoomInRight
}
@keyframes zoomInUp{
0%{opacity:0;transform:scale3d(0.1, 0.1, 0.1) translate3d(0, 1000px, 0);animation-timing-function:cubic-bezier(0.55, 0.055, 0.675, 0.19)
}
60%{opacity:1;transform:scale3d(0.475, 0.475, 0.475) translate3d(0, -60px, 0);animation-timing-function:cubic-bezier(0.175, 0.885, 0.32, 1)
}
}
.zoomInUp{animation-name:zoomInUp
}
@keyframes zoomOut{
0%{opacity:1
}
50%{transform:scale3d(0.3, 0.3, 0.3)
}
50%,to{opacity:0
}
}
.zoomOut{animation-name:zoomOut
}
@keyframes zoomOutDown{
40%{opacity:1;transform:scale3d(0.475, 0.475, 0.475) translate3d(0, -60px, 0);animation-timing-function:cubic-bezier(0.55, 0.055, 0.675, 0.19)
}
to{opacity:0;transform:scale3d(0.1, 0.1, 0.1) translate3d(0, 2000px, 0);transform-origin:center bottom;animation-timing-function:cubic-bezier(0.175, 0.885, 0.32, 1)
}
}
.zoomOutDown{animation-name:zoomOutDown
}
@keyframes zoomOutLeft{
40%{opacity:1;transform:scale3d(0.475, 0.475, 0.475) translate3d(42px, 0, 0)
}
to{opacity:0;transform:scale(0.1) translate3d(-2000px, 0, 0);transform-origin:left center
}
}
.zoomOutLeft{animation-name:zoomOutLeft
}
@keyframes zoomOutRight{
40%{opacity:1;transform:scale3d(0.475, 0.475, 0.475) translate3d(-42px, 0, 0)
}
to{opacity:0;transform:scale(0.1) translate3d(2000px, 0, 0);transform-origin:right center
}
}
.zoomOutRight{animation-name:zoomOutRight
}
@keyframes zoomOutUp{
40%{opacity:1;transform:scale3d(0.475, 0.475, 0.475) translate3d(0, 60px, 0);animation-timing-function:cubic-bezier(0.55, 0.055, 0.675, 0.19)
}
to{opacity:0;transform:scale3d(0.1, 0.1, 0.1) translate3d(0, -2000px, 0);transform-origin:center bottom;animation-timing-function:cubic-bezier(0.175, 0.885, 0.32, 1)
}
}
.zoomOutUp{animation-name:zoomOutUp
}
@keyframes slideInDown{
0%{transform:translate3d(0, -100%, 0);visibility:visible
}
to{transform:translateZ(0)
}
}
.slideInDown{animation-name:slideInDown
}
@keyframes slideInLeft{
0%{transform:translate3d(-100%, 0, 0);visibility:visible
}
to{transform:translateZ(0)
}
}
.slideInLeft{animation-name:slideInLeft
}
@keyframes slideInRight{
0%{transform:translate3d(100%, 0, 0);visibility:visible
}
to{transform:translateZ(0)
}
}
.slideInRight{animation-name:slideInRight
}
@keyframes slideInUp{
0%{transform:translate3d(0, 100%, 0);visibility:visible
}
to{transform:translateZ(0)
}
}
.slideInUp{animation-name:slideInUp
}
@keyframes slideOutDown{
0%{transform:translateZ(0)
}
to{visibility:hidden;transform:translate3d(0, 100%, 0)
}
}
.slideOutDown{animation-name:slideOutDown
}
@keyframes slideOutLeft{
0%{transform:translateZ(0)
}
to{visibility:hidden;transform:translate3d(-100%, 0, 0)
}
}
.slideOutLeft{animation-name:slideOutLeft
}
@keyframes slideOutRight{
0%{transform:translateZ(0)
}
to{visibility:hidden;transform:translate3d(100%, 0, 0)
}
}
.slideOutRight{animation-name:slideOutRight
}
@keyframes slideOutUp{
0%{transform:translateZ(0)
}
to{visibility:hidden;transform:translate3d(0, -100%, 0)
}
}
.slideOutUp{animation-name:slideOutUp
}
.onepx-t{position:relative
}
.onepx-t:after{position:absolute;top:0;left:0;content:'';width:100%;height:1px;background-color:#dadada;transform:scaleY(0.5)
}
.onepx-b{position:relative
}
.onepx-b:after{position:absolute;bottom:0;left:0;content:'';width:100%;height:1px;transform:scaleY(0.5);background-color:#dadada
}
.one-line{white-space:nowrap;overflow:hidden;text-overflow:ellipsis
}
.two-line{overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical
}
html{height:100%
}
body{margin:0;background-color:#eee
}
#app{color:#2c3e50;font-family:Source Sans Pro, Helvetica, sans-serif;padding-bottom:100px
}
#app a{text-decoration:none;color:#000
}



