
.expand{margin-bottom:10px
}
.expand .expand-text>p{overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-box-orient:vertical
}
.expand .expand-text>p:first-child{color:#555;margin-bottom:6px
}
.expand .toggle-btn{position:relative;height:30px;margin-top:10px
}
.expand .toggle-btn:after{content:'';position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);border-top:8px solid transparent;border-bottom:8px solid transparent;border-right:8px solid transparent;border-left:8px solid transparent
}
.expand .up-arrow:after{border-bottom-color:#555;margin-top:-8px
}
.expand .down-arrow:after{border-top-color:#555
}



