
.show-item{overflow:hidden;display:inline-block;max-width:100px;box-sizing:border-box
}
@media (max-width: 320px){
.show-item{max-width:90px
}
}
@media (min-width: 380px) and (max-width: 420px){
.show-item{max-width:116px
}
}
.show-item .img-box{width:100px;height:140px;background-repeat:no-repeat;background-size:cover;border:1px solid #d4cecd
}
@media (max-width: 320px){
.show-item .img-box{width:90px;height:130px
}
}
@media (min-width: 380px) and (max-width: 420px){
.show-item .img-box{width:116px;height:150px
}
}
.show-item .show-title{margin-top:10px;margin-bottom:0;font-weight:normal;font-size:14px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis
}



